import * as React from 'react';
import { SimpleLazyLoad } from '@ali/4ever-component';
import { CommentPluginConfigs } from '../types';

export default function createRenderContent(
  configs: CommentPluginConfigs | undefined,
) {
  const LazyLoad = configs?.lazyload || SimpleLazyLoad;
  const BubblesNew = LazyLoad(() => import('../components/BubblesNew'));
  return function renderContent(_, controller, next) {
    if (configs?.isGrayed && !configs.isGrayed()) {
      return next();
    }
    const isDisabled = Boolean(configs?.isDisabled && configs.isDisabled());
    if (isDisabled) {
      return next();
    }
    return (
      <React.Fragment>
        {next()}
        <BubblesNew configs={configs} controller={controller} />
      </React.Fragment>
    );
  };
}
